import UserModel from '@/models/user.js';
import ListItem from '@/components/ListItem/ListItem.vue'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
const userModel = new UserModel() 
export default {
	data(){
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500
		}
	},
	onReady() {
		
	}, 
	components:{
		ListItem,
		uniPopup
	},
	methods:{
		chooseImage(){
			uni.chooseImage({
				success(res) {
					const tempFilePath = res.tempFilePaths
					uni.uploadFile({
						url:'http://baidu.com',
						filePath:tempFilePath[0],
						name:'file',
						formData:{
							
						},
						success(uploadFileRes){
							console.log(uploadFileRes.data)
						}
					})
				}
			})
		},
		getUserInfo(e){
			console.log(e)
		},
		getPhoneNumber(e){
			const { iv, encryptedData, errMsg } = e.detail
			if( errMsg !== 'getPhoneNumber:ok' ){
				uni.showToast({
					title:'授权失败，请重新授权'
				})
			}
			// 以下执行登录,需要后台提供一个接口
			uni.login({
				success(response) {
					if(response.errMsg === ''){
						
						// 此处执行后台登录过程，传递的参数自己斟酌
						userModel.wxLogin({
							data:{
								encryptedData,
								ivData: iv,
								code: response.code
							},
							success(res){
								console.log(res)
								// 此处应返回前端信息
								if (res.data.code == 0) {
															  
								}else{
									uni.showToast({
										title:'登录失败，请重新授权'
									})
								}
								
							}
						})	
					}else{
						uni.showToast({
							title:'获取code失败'
						})
					}
				}
			})
		},
		showDetail(){
			let login = false
			// 已登录
			if(login){
				
				return
			}
			
			// 未登录
			this.$refs.noticeLogin.open()
		}
	}
}
